    function changeColor(colorGroup){

        let css_variables = Object.keys(colors[colorGroup]);
        for(let i = 0; i < css_variables.length; i++)
        {
            document.documentElement.style.setProperty(css_variables[i], colors[colorGroup][css_variables[i]]);
        }
    }
    
